.home {
  display: flex;
  flex-wrap: wrap;
  gap: 1.5rem;
  min-height: 100vh;
  align-items: center;
  background: url('https://pbs.twimg.com/media/FdwHUkDWQAMI4It?format=jpg&name=large')
    no-repeat;

  background-size: cover;
  background-position: center;
  color: #9ca0ab;
  padding: 20px;
}

.home .content {
  flex: 1 1 40rem;
}

.home .image {
  flex: 1 1 40rem;
}

.home .image img {
  width: 100%;
  padding: 1rem;
  animation: float 3s linear infinite;
}

@keyframes float {
  0%,
  100% {
    transform: translateY(0rem);
  }

  50% {
    transform: translateY(3rem);
  }
}

.home .content span {
  font-size: 5rem;
  color: #fac031;
}

.home .content p {
  font-size: 2rem;
  color: #666;
  padding: 1rem 0;
}

.flag {
  color: #666;
}

.bg-flag {
  background-color: #fac031;
}
